
.app{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 70%;               
   max-width: 700px;          
   margin: 20px auto;         
   padding: 20px;             
   border-radius: 10px;       
   background: linear-gradient(90deg, rgb(189, 66, 183) 0%, rgb(172, 18, 156) 0%, rgb(83, 3, 75) 100%, rgba(119,9,121,1) 100%); 

}
.App-title
{
    font-weight: 400;
    color: white;
    font-family: "Itim", cursive;
    font-weight: 400;
    font-style: normal;
    text-decoration: underline;
}
.App-title:hover{
    scale: 1.1;
    cursor:default;
    color:red;
}
.input{
    background-color: rgb(226, 211, 211);
    padding: 10px;
    border-radius:5px ;
    margin-top: 30px;
    box-shadow: 0px 3px 5px 3px rgba(0,0,0,0.2);

}
.input input{
    background-color: transparent;
    border: none;
    outline: none;
    width: 300px;
    font-size: 16px
}
.input button{
    background-color:rgb(10, 94, 179) ;
    color: white;
    text-transform:uppercase ;
    font-size: 15px;
    border: none;
    outline:none;
    padding: 5px 20px;
    font-weight:300;
    letter-spacing: 2px;
    cursor:pointer;
    box-shadow:0px 3px 5px 3px rgba(107, 104, 104);

}
.input button:hover{
    scale: 0.95;
}
.item-container{
    min-height: 350px;
    min-width: 400px;
    background-color: rgb(214, 200, 200);
    padding: 10px 20px;
    margin-top: 60px;
    border-radius: 5px;
    box-shadow:0px 3px 5px 3px rgb(233, 153, 153);
}
.todoitem{
    display: flex;
    justify-content: space-between; 
    border-bottom: 1px solid rgb(172, 168, 168);
    padding-bottom: 10px;
    margin: 10px 0;
    font-family:'Times New Roman', Times, serif;
}
.todoitem p{
    font-size: 23px;
    color: rgb(6, 6, 99);

}

.todoitem .delete{
    background-color: rgb(209, 25, 25);
    color: white;
    border: none;
    outline: none;
    padding: 5px 20px;
    cursor: pointer;
    box-shadow:0px 3px 5px 3px rgba(107, 104, 104);
}
.todoitem .edit{
    background-color: rgb(17, 112, 4);
    color: white;
    border: none;
    outline: none;
    padding: 5px 20px;
    cursor: pointer;
    box-shadow:0px 3px 5px 3px rgba(107, 104, 104);
    margin-left: 7px;
}
.empty{
    color: red;
    display: flex;
    align-items: center;
    justify-content: center;
}